.title{
    color:greenyellow;
}
#capture{
    position: absolute;
    right: 190px;
    bottom: -40px;

}
#video{
    position: absolute;
    right: 0;
    top: 0;
}
#img{
    position: absolute;
    left: 0;
    top: 0;
    width:480px;
    height:320px;
    margin-left:20px;
}
.auto{
    position: absolute;
    left: 50%;
    top: 50%;
    height: 320px;
    margin-top: -160px;
}
#sure{
    position: absolute;
    left: 19px;
    bottom: -40px;

}
button{
    cursor: pointer;
    margin: 0 auto;
    border: 1px solid #f0f0f0;
    background: #5cacee;
    color: #FFF;
    width: 100px;
    height: 36px;
    line-height: 36px;
    border-radius: 8px;
    text-align: center;
    /*禁止选择*/
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Chrome/Safari/Opera */
    -khtml-user-select: none; /* Konqueror */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently not supported by any browser */
}